import React, { useEffect, useState } from "react";
import { collection, getDocs } from "firebase/firestore";
import db from "../firebase/firebaseConfig";
import IndexContext from "./IndexContext";

const IndexState = (props) => {
  const [data, setData] = useState([]);
  const [carrusel, setCarrusel] = useState([]);
  useEffect(() => {
    const obtenerDatos = async () => {
      const datos = await getDocs(collection(db, "producto"));
      const datosCarrusel = await getDocs(collection(db, "galeria"));

      setData(datos.docs);
      setCarrusel(datosCarrusel.docs);
    };
    obtenerDatos();
  }, []);
  return (
    <IndexContext.Provider value={{ data: data, carrusel: carrusel }}>
      {props.children}
    </IndexContext.Provider>
  );
};

export default IndexState;
