import React, { useContext } from "react";
import Masonry from "react-masonry-css";
import IndexContext from "../context/IndexContext";

const Cuerpo = () => {
  const { carrusel } = useContext(IndexContext);
  const breakpoints = {
    default: 4,
    1100: 3,
    700: 2,
    500: 1,
  };
  return (
    <>
      <div className="container">
        <Masonry
          breakpointCols={breakpoints}
          className="my-masonry-grid"
          columnClassName="my-masonry-grid_column"
        >
          {carrusel.map((item, intex) => {
            return (
              <div
                key={intex}
                className="shadow-lg p-3 mb-5 bg-white card__galeria"
              >
                <img src={item.data().foto} className="img__galeria" />
              </div>
            );
          })}
        </Masonry>
      </div>
    </>
  );
};

export default Cuerpo;
