import React, { useContext } from "react";
import Slider from "./Slider";
import Typewriter from "typewriter-effect";
import { FaInstagram } from "react-icons/fa";
import { FaTwitter } from "react-icons/fa";
import { FaGooglePlusG } from "react-icons/fa";
import { FaFacebookF } from "react-icons/fa";
import { FaGlobeAmericas } from "react-icons/fa";
import IndexContext from "../context/IndexContext";

const Header = () => {
  const { data } = useContext(IndexContext);

  return (
    <>
      <div className="col-md-5 ">
        <div className="text-header d-flex">
          <div className="container">
            <div className="row justify-content-center align-items-center row__header ">
              <Slider data={data} />
            </div>
          </div>
        </div>
      </div>
      <div className="col-md-7  body__header">
        <div className="text-header d-flex">
          <div className="container">
            <div className="row justify-content-center align-items-center row__header">
              <div className="col-sm-8 ">
                <span className="titulo__confecciones">CONFECCIONES</span>
                <h1 className="titulo__h1">
                  <Typewriter
                    options={{
                      strings: ["Ramos", "WhatsApp:", "991612422"],
                      autoStart: true,
                      loop: true,
                    }}
                  />
                </h1>
                <p className="sub__titulo__header">
                  ternos, Uniformes, Banderas
                </p>

                <ul className="list-unstyled">
                  <li>
                    <a
                      href="https://www.facebook.com/Confecciones-Ramos-186006995241496"
                      target="”_blank”"
                    >
                      <FaFacebookF />
                    </a>
                  </li>
                  <li>
                    <a href="!#">
                      <FaTwitter />
                    </a>
                  </li>
                  <li>
                    <a
                      href="https://www.instagram.com/confeccionesramos/"
                      target="”_blank”"
                    >
                      <FaInstagram />
                    </a>
                  </li>

                  <li>
                    <a
                      href="mailto: jolurn7@gmail.com?Subject=CconfeccionesRamos"
                      target="”_blank”"
                    >
                      <FaGooglePlusG />
                    </a>
                  </li>
                  <li>
                    <a href="!#">
                      <FaGlobeAmericas />
                    </a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="col-md-12">
        <div style={{ height: "150px", overflow: "hidden" }}>
          <svg
            viewBox="0 0 500 150"
            preserveAspectRatio="none"
            style={{ height: "100%", width: "100%" }}
          >
            <path
              d="M0.00,49.98 C149.99,150.00 349.20,-49.98 500.00,49.98 L500.00,150.00 L0.00,150.00 Z"
              style={{ stroke: "none", fill: "#fff" }}
            ></path>
          </svg>
        </div>
      </div>
    </>
  );
};

export default Header;
